html {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, p, body {
  margin: 0;
  padding: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  width: 16px;
  height: 16px;
}

.product {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
}

.product__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-top: 16px;
  display: flex;
}

.product__rating-reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product__price-description {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product__price-cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__button-buy-link {
  cursor: pointer;
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 0 auto;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product__button-buy-link:hover {
  box-sizing: border-box;
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

